@import "./global.scss";

.App {
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, var(--background-color-bottom) 0%, var(--background-color-top) 100%);
  position: absolute;
  top: 0;
  overflow: hidden;
  text-align: center;
  display: flex;
  justify-content: center;

  @include for-desktop {
    min-height: 100% !important;
  }

  .websiteContent {
    width: 100%;
    max-width: 1200px;
    height: 100%;
    position: absolute;
    top: 0;
    overflow: hidden;
    text-align: center;

    .noData {
      width: 100%;
      height: 100%;
      color: var(--text-color);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: hidden;
    }

    .sections {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      touch-action: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
      }

      @include for-desktop {
        scroll-behavior: auto;
        scroll-snap-type: y mandatory;
        scroll-snap-stop: always;
      }

      @include for-mobile {
        overflow: hidden;
      }

      @include for-mobile {
        * {
          pointer-events: none;
        }
      }

      .container {
        width: calc(100% - 20px);
        height: 100%;
        margin-left: 10px;
        position: absolute;
        padding-top: 60px;
        touch-action: none;
        transition: all 400ms cubic-bezier(.86,0,.07,1) 0ms;
        -webkit-transition: all 400ms cubic-bezier(.86,0,.07,1) 0ms;
        -moz-transition: all 400ms cubic-bezier(.86,0,.07,1) 0ms;
        -o-transition: all 400ms cubic-bezier(.86,0,.07,1) 0ms;

        @include for-desktop {
          scroll-snap-align: start;
        }
      }
    }
  }
}
